_vm._v("Las bases curriculares en la educación inicial desempeñan un papel fundamental en la orientación de las prácticas pedagógicas, buscando asegurar una formación integral de los niños y niñas. Estas bases no solo se enfocan en el desarrollo cognitivo, sino también en las áreas emocional, física y social de los estudiantes. A continuación, se explican los principales ejes y componentes que conforman estas bases, los cuales le permitirán comprender cómo estructurar las actividades educativas de forma efectiva.")]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Los ejes principales de las bases curriculares son:")]),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slider-1.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Desarrollo integral")]),_c('p',[_vm._v("Uno de los principios esenciales de las bases curriculares es promover el desarrollo integral de los niños. Esto implica que la educación inicial debe atender a todas las dimensiones del ser humano, no solo la cognitiva. De acuerdo con la Ley 1098 del 2006 (Código de la Infancia y la Adolescencia), se reconoce que el bienestar de los niños está ligado a su desarrollo emocional, social y físico, por lo que la educación inicial debe contribuir al crecimiento de todas estas áreas de forma simultánea.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slider-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Enfoque de derechos")]),_c('p',[_vm._v("Las bases curriculares se fundamentan en el enfoque de derechos, lo que significa que la educación debe garantizar el acceso de todos los niños a una educación de calidad, sin importar su contexto social, cultural o geográfico. La Constitución de 1991 y el Código de la Infancia y la Adolescencia, en su Artículo 44, establecen que los niños tienen derecho a la educación. Esto implica que se debe asegurar que todos los niños reciban una formación adecuada, respetando su individualidad y diversidad.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slider-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Contextualización")]),_c('p',[_vm._v("En Colombia, se reconoce que cada niño vive en un contexto único, lo cual debe ser tomado en cuenta al momento de planificar las actividades educativas. Esto significa que la educación no puede ser homogénea, sino que debe adaptarse a las diferentes realidades sociales, culturales y geográficas del país. El Decreto 1280 del 2018 resalta la importancia de contextualizar la educación inicial, buscando la inclusión de todos los niños, independientemente de su lugar de residencia, sea urbano, rural o de comunidades étnicas.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/slider-2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-lg-1"},[_c('h5',[_vm._v("Aprendizaje significativo")]),_c('p',[_vm._v("Un principio central de las bases curriculares es la búsqueda de un aprendizaje significativo. Esto implica que las experiencias educativas deben estar conectadas con los conocimientos previos de los niños y ser relevantes para sus vidas cotidianas. Según las teorías del aprendizaje constructivista, como las de Piaget y Vygotsky, el aprendizaje se construye de manera activa, por lo que las actividades deben promover la reflexión, la acción y la interacción con el entorno para que los niños logren integrar lo aprendido de forma efectiva.")])])])])],1)]),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Y los componentes claves de las bases curriculares son:")]),_c('TabsC',{staticClass:"color-acento-contenido mb-5"},[_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titulo":"Competencias","icon":require('@/assets/bullets/icon-tab-c-1.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 col-lg-7 col-xl-8 mb-4 mb-md-0 order-2"},[_c('h4',[_vm._v("Competencias")]),_c('p',[_vm._v("Las bases curriculares se orientan hacia el desarrollo de competencias, que son un conjunto de conocimientos, habilidades y actitudes que los niños deben adquirir durante su proceso educativo. Estas competencias no solo se limitan a la dimensión cognitiva, sino que también incluyen el desarrollo de habilidades sociales, emocionales y motoras. Se busca que los niños se preparen para afrontar diversos desafíos en su vida cotidiana, desarrollando competencias que les permitan interactuar con el mundo de manera autónoma y creativa.")])]),_c('div',{staticClass:"col-md-6 col-lg-5 col-xl-4 order-1"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema1/tab-1.png"),"alt":""}})])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titulo":"Ámbitos de desarrollo","icon":require('@/assets/bullets/icon-tab-c-1.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 col-lg-7 col-xl-8 mb-4 mb-md-0 order-2"},[_c('h4',[_vm._v("Ámbitos de desarrollo")]),_c('p',[_vm._v("Los ámbitos de desarrollo son áreas específicas del crecimiento de los niños, que incluyen el desarrollo físico, cognitivo, social, emocional y lingüístico. Las bases curriculares detallan cómo promover el desarrollo en cada uno de estos ámbitos, entendiendo que todos son interdependientes. Por ejemplo, en el ámbito del lenguaje, se busca que los niños adquieran habilidades de comunicación verbal y no verbal que les permitan expresar sus ideas y emociones de manera efectiva.")])]),_c('div',{staticClass:"col-md-6 col-lg-5 col-xl-4 order-1"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema1/tab-2.png"),"alt":""}})])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titulo":"El rol de la familia","icon":require('@/assets/bullets/icon-tab-c-1.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 col-lg-7 col-xl-8 mb-4 mb-md-0 order-2"},[_c('h4',[_vm._v("El rol de la familia")]),_c('p',[_vm._v("Las bases curriculares también reconocen el rol fundamental de la familia en el proceso educativo. Los primeros años de vida son decisivos para el aprendizaje, y es por eso que se promueve una colaboración estrecha entre la familia y la institución educativa. La implicación de los padres y cuidadores es clave para garantizar que los niños reciban el apoyo necesario tanto dentro como fuera del entorno escolar, lo que favorece un desarrollo integral.")])]),_c('div',{staticClass:"col-md-6 col-lg-5 col-xl-4 order-1"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema1/tab-3.png"),"alt":""}})])])])]),_c('div',{staticClass:"tabs-content border px-4 p-md-5 py-3",attrs:{"titulo":"Evaluación formativa","icon":require('@/assets/bullets/icon-tab-c-1.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 col-lg-7 col-xl-8 mb-4 mb-md-0 order-2"},[_c('h4',[_vm._v("Evaluación formativa")]),_c('p',[_vm._v("La evaluación en la educación inicial no debe centrarse exclusivamente en los resultados finales, sino en el proceso de aprendizaje de los niños. La evaluación formativa permite monitorear el progreso de los estudiantes, identificar sus necesidades y ajustar las estrategias pedagógicas para mejorar su aprendizaje. La evaluación no solo mide lo que el niño sabe, sino también cómo está aprendiendo, lo que permite a los educadores ofrecer una educación más personalizada y ajustada a las necesidades de cada uno.")])]),_c('div',{staticClass:"col-md-6 col-lg-5 col-xl-4 order-1"},[_c('figure',{attrs:{"data-aos":"zoom-in"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema1/tab-4.png"),"alt":""}})])])])])]),_vm._m(16),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Comprender los ejes y componentes de las bases curriculares le permite crear un entorno educativo que favorezca el desarrollo integral de los niños. Al aplicar estos principios en la práctica pedagógica, contribuye a que los niños crezcan no solo en el aspecto académico, sino también en sus habilidades sociales, emocionales y físicas, asegurando una educación de calidad e inclusiva.")]),_vm._m(17),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Las bases curriculares en la educación inicial, juegan un papel fundamental en el desarrollo integral de los niños, porque no solo organizan el contenido que se debe enseñar, sino que también guían la forma en que los educadores interactúan con los niños, promoviendo un entorno de aprendizaje inclusivo, respetuoso y estimulante. El rol de estas bases va más allá de ser un simple conjunto de pautas académicas; su objetivo principal es asegurar que los procesos de enseñanza y aprendizaje favorezcan el crecimiento completo del niño, abarcando sus dimensiones cognitivas, emocionales, físicas y sociales. De esta manera, las bases curriculares contribuyen a que los niños interactúen con su entorno, comprendan el mundo que les rodea y construyan una identidad personal sólida.")]),_c('div',{staticClass:"row align-items-start mb-5"},[_c('div',{staticClass:"col-lg-7"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Desarrollo cognitivo"}},[_c('p',[_vm._v("En cuanto al desarrollo cognitivo, las bases curriculares ofrecen las orientaciones necesarias para que los educadores diseñen experiencias educativas que estimulen el pensamiento crítico, la resolución de problemas y la creatividad. La educación en la primera infancia debe ir más allá de la memorización de contenidos y fomentar habilidades cognitivas que permitan a los niños organizar y aplicar el conocimiento en diversas situaciones. Por ejemplo, en el diseño de actividades de aprendizaje, las bases curriculares proponen la importancia de ofrecer experiencias sensoriales y manipulativas que favorezcan el desarrollo de la inteligencia lógico-matemática y espacial. Además, se destaca la necesidad de fomentar la lectoescritura temprana, estableciendo una base sólida para el aprendizaje de la lectura y la escritura en etapas posteriores.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Desarrollo emocional y social"}},[_c('p',[_vm._v("El desarrollo emocional y social también ocupa un lugar crucial dentro de las bases curriculares. Estas bases promueven la autonomía, el sentido de pertenencia y el trabajo en equipo entre los niños. De hecho, las habilidades socioemocionales que se cultivan en la educación inicial tienen un impacto directo en el bienestar de los niños, ayudándoles a gestionar sus emociones, a expresar sus sentimientos de manera adecuada y a desarrollar relaciones interpersonales saludables. Las actividades que integran el juego, la interacción con otros niños y el trabajo colaborativo son esenciales en este proceso. Según el Ministerio de Educación Nacional (2015), el aprendizaje en la primera infancia debe incluir actividades que favorezcan la empatía, la resolución de conflictos y el respeto por las diferencias, contribuyendo a la formación de una comunidad educativa inclusiva.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Enfoque en el desarrollo físico"}},[_c('p',[_vm._v("Otro aspecto relevante del rol de las bases curriculares es su enfoque en el desarrollo físico de 